<template>
  <v-container>
    <h1>„É¨„ÉÉ„Çπ„É≥„ÇíÂßã„ÇÅ„Çà„ÅÜ</h1>
    <v-divider></v-divider>

    <!-- ÂçòÂÖÉÈÅ∏Êäû (ÈÅ∏ÊäûÂæå„É≠„ÉÉ„ÇØ) -->
    <h2 v-if="!gameStarted">„Åæ„Åö„ÅØÂçòÂÖÉ„ÇíÈÅ∏Êäû„Åó„Çà„ÅÜ</h2>

    <v-select v-model="selectedLesson" :items="lessons" item-title="name" item-value="id" label="ÂçòÂÖÉ„ÇíÈÅ∏Êäû" class="mb-4"
      @update:model-value="onLessonSelect" :disabled="gameStarted">
    </v-select>

    <v-btn v-if="selectedLesson && !gameStarted" @click="startGame" color="primary">
      „Ç≤„Éº„É†„Çπ„Çø„Éº„Éà
    </v-btn>

    <v-card v-if="currentSentence && gameStarted">
      <v-card-title>ÊñáÁ´†„ÇíÊ≠£„Åó„Åè‰∏¶„ÅπÊõø„Åà„Å¶„Åè„Å†„Åï„ÅÑ</v-card-title>
      <v-card-text>
        <v-chip-group v-model="selectedWords" column multiple>
          <v-chip v-for="word in shuffledWords" :key="word" :value="word"
            :style="getChipStyle(word.trim(), currentIndex)" variant="elevated">
            {{ word }}
          </v-chip>
        </v-chip-group>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-text>
        <v-card-title>
          „ÅÇ„Å™„Åü„ÅÆÂõûÁ≠î
        </v-card-title>
        <v-chip-group column multiple>
          <v-chip v-for="word in selectedWords" :key="word" :value="word"
            :style="getChipStyle(word.trim(), currentIndex)" variant="outlined">
            {{ word }}
          </v-chip>
        </v-chip-group>
      </v-card-text>

      <v-card-actions v-if="currentSentence.length > 0">
        <v-btn @click="popString" color="primary">
          1„Å§Êàª„Çã
        </v-btn>
      </v-card-actions>

      <v-card-actions v-if="checkLength()">
        <v-btn @click="checkAnswer" color="primary">
          ÂõûÁ≠î„ÇíÁ¢∫Ë™ç
        </v-btn>
      </v-card-actions>
    </v-card>

    <v-alert v-if="feedback" :type="feedbackType" class="mt-4">
      {{ feedback }}
    </v-alert>

    <div v-if="gameEnded">
      <v-btn @click="resetGame" color="primary">„ÇÇ„ÅÜ‰∏ÄÂ∫¶„Éó„É¨„Ç§</v-btn>
    </div>
  </v-container>
</template>

<script>
import { db } from '../firebase/init'
import { collection, getDocs } from 'firebase/firestore'

export default {
  name: 'PlayScreen',
  data() {
    return {
      lessons: [],
      selectedLesson: null,
      sentences: [],
      currentSentence: null,
      currentIndex: 0,
      wordColors: [],
      shuffledWords: [],
      selectedWords: [],
      feedback: '',
      feedbackType: 'info',
      gameStarted: false,
      gameEnded: false,
      startTime: 0,
      elapsedTime: 0,
      colorScheme: {
        subject: '#FFF59D',
        verb: '#A5D6A7',
        object: '#90CAF9',
        complement: '#A5D6A7',
        other: '#E0E0E0'
      },
    }
  },
  async mounted() {
    await this.loadLessons()
  },
  methods: {
    getChipStyle(word, sentenceIndex) {
      const backgroundColor = this.wordColors[sentenceIndex]?.[word] || this.colorScheme.other;
      return {
        backgroundColor: backgroundColor,
        color: this.getContrastColor(backgroundColor),
        borderColor: backgroundColor,
      }
    },

    getContrastColor(hexColor) {
      const r = parseInt(hexColor.slice(1, 3), 16);
      const g = parseInt(hexColor.slice(3, 5), 16);
      const b = parseInt(hexColor.slice(5, 7), 16);
      const brightness = (r * 299 + g * 587 + b * 114) / 1000;
      return brightness > 128 ? '#000000' : '#FFFFFF';
    },

    async loadLessons() {
      try {
        const lessonsRef = collection(db, 'lessons')
        const querySnapshot = await getDocs(lessonsRef)
        this.lessons = querySnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }))
      } catch (error) {
        console.error('LessonsË™≠„ÅøËæº„Åø„Ç®„É©„Éº:', error)
      }
    },

    onLessonSelect() {
      const lesson = this.lessons.find(l => l.id === this.selectedLesson)
      if (lesson) {
        this.sentences = [...lesson.sentences]
        this.wordColors = [...lesson.wordColors]
      }
    },

    startGame() {
      this.gameStarted = true
      this.startTime = Date.now()
      this.nextSentence()
    },

    nextSentence() {
      if (this.sentences.length > 0) {
        this.currentSentence = this.sentences.pop()
        this.shuffledWords = this.shuffleArray(this.currentSentence.split(','))
        this.selectedWords = []
        this.feedback = ''
        this.feedbackType = 'info'
        this.currentIndex = this.sentences.length
      } else {
        this.endGame()
      }
    },

    endGame() {
      this.gameEnded = true
      this.elapsedTime = Math.floor((Date.now() - this.startTime) / 1000)
      this.feedback = `„Åô„Åî„ÅÑÔºÅ${this.elapsedTime}Áßí„ÅßÁµÇ‰∫Ü„Åó„Åæ„Åó„ÅüÔºÅ`
      this.feedbackType = 'success'
    },

    resetGame() {
      this.gameStarted = false
      this.gameEnded = false
      this.selectedLesson = null
      this.currentSentence = null
      this.elapsedTime = 0
    },

    shuffleArray(array) {
      for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1))
          ;[array[i], array[j]] = [array[j], array[i]]
      }
      return array
    },

    isCorrect() {
      const correctAnswer = this.currentSentence.split(',').join(' ')
      const userAnswer = this.selectedWords.join(' ')
      return correctAnswer === userAnswer
    },

    checkAnswer() {
      if (this.isCorrect()) {
        this.feedback = 'Ê≠£Ëß£„Åß„ÅôÔºÅ'
        this.feedbackType = 'success'
        this.nextSentence()
      } else {
        this.feedback = `üò£ÊÆãÂøµÔºéÔºéÔºé„ÇÇ„ÅÜ‰∏ÄÂ∫¶„ÇÑ„Å£„Å¶„Åø„Çà„ÅÜÔºÅüò£`
        this.feedbackType = 'error'
      }
    },
    checkLength() {
      const correctAnswer = this.currentSentence.split(',').join(' ')
      const userAnswer = this.selectedWords.join(' ')
      return (userAnswer.length == correctAnswer.length)
    },

    popString() {
      this.selectedWords.pop()
    },
  }
}
</script>
